  <div class="tgio1Description">
    <div class="textColumn">
      <div class="myH3">{{ $t("tgio1") }}</div>
      <div class="ulContainer">
        <ul class="myH5" v-html="$t('tgio1C1')"></ul>
      </div>
      <div class="setTable">
        <div class="setImgBlock">
          <img class="setImg" src="@/assets/products/robotics/tgio1Img1.webp" />
          <img class="setImg" :src="tgio1Img2" />
        </div>
      </div>
    </div>
  </div>
</template>

