<script>
import { watch, ref } from "vue";
import { useI18n } from "vue-i18n";
export default {
  setup() {
    // 切換不同語系圖片
    const { locale } = useI18n();

    watch(locale, () => {
      changeImg();
    });
    const tgio1Img2 = ref("");

    const changeImg = () => {
      if (locale.value == "zh") {
        tgio1Img2.value = require("@/assets/products/robotics/tgio1Img2_zh.webp");
      } else {
        tgio1Img2.value = require("@/assets/products/robotics/tgio1Img2_en.webp");
      }
    };

    changeImg();

    return { tgio1Img2 };
  },
};
</script>
